import { Card, Typography, Theme } from '@mui/material';
import { makeStyles } from '@mui/styles';
import React from 'react';

const useStyles = makeStyles<Theme>(({ breakpoints, spacing }) => ({
  root: {
    border: 'none',
    padding: spacing(2),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    cursor: 'pointer',
    transition: 'transform .2s ease-in',
    '&:hover': {
      transform: 'translateY(-10px)',
    },
  },
  content: {
    padding: 0,
  },
  title: {
    marginBottom: 16,
    fontSize: 20,
    textAlign: 'center',
    whiteSpace: 'pre-line',
    [breakpoints.down('md')]: {
      fontSize: 18,
    },
  },
  image: {
    width: 140,
    marginBottom: spacing(2),
  },
  description: {
    whiteSpace: 'pre-line',
    fontSize: 14,
  },
}));

interface Props {
  title: string;
  description?: string;
  image: string;
  onClick?: () => void;
}
const BenefitCard = ({ title, description, image, onClick }: Props) => {
  const styles = useStyles();
  return (
    <Card className={styles.root} onClick={onClick}>
      <img src={image} className={styles.image} alt={title} />
      <Typography variant="h6" className={styles.title}>
        {title}
      </Typography>
      {description ? <Typography className={styles.description}>{description}</Typography> : null}
    </Card>
  );
};

export default BenefitCard;
